#root{
    background-color: #12a1e4;
}

#calcGrid{
    padding-top: 50px;
    padding-bottom: 50px;
    margin: 0 auto;
    height:100vh;
    width:80vw;
    display:grid;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr 1fr;
   }
  
   
.button{
    width:100%;
    height:100%;
   }
   #display{
    width:100%;
    background-color: white;
    border: 2px solid black;
    display:flex;
    justify-content:flex-end;
    align-items:center;
    font-size:70px;
    grid-row:1/2;
    grid-column:1/5;
   }
   #divide{
    grid-row:2/3;
    grid-column:2/3;
   }
   #multiply{
    grid-row:2/3;
    grid-column:3/4;
   }
   #subtract{
    grid-row:2/3;
    grid-column:4/5;
   }
   #seven{
    grid-row:3/4;
    grid-column:1/2;
   }
   #eight{
    grid-row:3/4;
    grid-column:2/3;
   }
   #nine{
    grid-row:3/4;
    grid-column:3/4;
   }
   #four{
    grid-row:4/5;
    grid-column:1/2;
   }
   #five{
    grid-row:4/5;
    grid-column:2/3;
   }
   #six{
    grid-row:4/5;
    grid-column:3/4;
   }
   #one{
    grid-row:5/6;
    grid-column:1/2;
   }
   #two{
    grid-row:5/6;
    grid-column:2/3;
   }
   #three{
    grid-row:5/6;
    grid-column:3/4;
   }
   #zero{
    grid-row:6/7;
    grid-column:1/3;
   }
   #decimal{
    grid-row:6/7;
    grid-column:3/4;
   }
   #add{
    grid-row:3/5;
    grid-column:4/5;
   }
   #equals{
    grid-row:5/7;
    grid-column:4/5;
   }
   #clear{
    grid-row:2/3;
    grid-column:1/2;
   }